@import '../../assets/styles/00-abstracts/abstracts';@import '../../assets/styles/01-mixins/mixins';.zzz-o-avatar {
  padding: px2rem(20px) px2rem(38px);
  background: $white;
  box-shadow: px2rem(3px) px2rem(4px) px2rem(12px) px2rem(1px) rgba(0, 0, 0, 0.25);
  border-radius: px2rem(5px);

  &__image {
    margin: 0 auto;
    width: px2rem(175px);
    height: px2rem(175px);
    border: px2rem(1px) solid $black;
    border-radius: 50%;
    overflow: hidden;

    img {
      transition: all 0.4s ease;
    }
  }

  &:hover &__image {
    img {
      transform: scale(1.05);
    }
  }

  &__info {
    text-align: left;
    margin-top: px2rem(19px);
    width: px2rem(202px);
    display: flex;
    flex-direction: column;
    gap: px2rem(17px);
  }
}
